/**
 * GLOBALS
 */
@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
}

section {
  max-width: 1110px;
  margin: 0 auto;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

h1 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 700;
  font-size: 200px;
  line-height: 200px;
  letter-spacing: -5px;
  color: #EEEFF4;
}

h2 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: -2px;
  color: #1B1D23;
}

h3 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 56px;
  line-height: 56px;
  letter-spacing: -2px;
  color: #1B1D23;
}

h4 {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #1B1D23;
}

p {
  font-family: "League Spartan", Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: #60636D;
}

a {
  font-size: 15px;
  font-family: "League Spartan", Arial, sans-serif;
  text-decoration: none;
  cursor: pointer;
}

li {
  list-style: none;
}

.button {
  background: #1B1D23;
  padding: 24px 32px;
  cursor: pointer;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
}
.button p {
  color: white;
  margin: 0;
  line-height: 0;
}

.button:hover {
  background-color: #60636D;
  transition: 0.3s;
}

.header .header__container {
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 56px 0;
}
