  flex-direction: column;
  justify-content: center;
  padding-left: 190px;
  align-items: flex-start;
  position: relative;
}
.herobanner .herobanner__splide .splide__slide::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  opacity: 0.25;
}
.herobanner .herobanner__splide .splide__slide .headings,
.herobanner .herobanner__splide .splide__slide .description,
