    padding: 0 32px;
  }
  .herobanner .herobanner__splide .splide__slide .headings h1 {
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1.2px;
  }
  .herobanner .herobanner__splide .splide__slide .description p {
    font-size: 15px;
    line-height: 25px;
  }
  .herobanner .herobanner__splide .splide__pagination {
